/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-nested-ternary */
import { Grid, Container } from '@mui/material';
import { useLocation, useNavigate } from 'react-router';
import useSettings from '../../hooks/useSettings';
import Page from '../../components/Page';
import {
  AnalyticsCurrentVisits,
  AnalyticsWebsiteVisits,
  AnalyticsWidgetSummary,
} from '../../sections/@dashboard/general/analytics';

export default function GeneralAnalytics() {
  const { themeStretch } = useSettings();
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const isAdmin = pathname.includes('/admin')

  return (
    <Page title="Dashboard">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Grid container spacing={3}>
          <>
            {isAdmin ?
              <>
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={3}
                  onClick={() => {
                    navigate('/dashboard/home');
                  }}
                >
                  <AnalyticsWidgetSummary
                    title="User"
                    total={71}
                    icon={'material-symbols-light:subscriptions-rounded'}
                  />
                </Grid>
                {/* <Grid
                  item
                  xs={12}
                  sm={6}
                  md={2}
                  onClick={() => {
                    navigate('/dashboard/subcription');
                  }}
                >
                  <AnalyticsWidgetSummary title="Subscriptions" total={131} color="info" icon={'octicon:organization-16'} />
                </Grid> */}
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={3}
                  onClick={() => {
                    // navigate('/dashboard/subcription');
                  }}
                >
                  <AnalyticsWidgetSummary title="PDF" total={131} color="info" icon={'octicon:organization-16'} />
                </Grid>
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={3}
                  onClick={() => {
                    navigate('/dashboard/home');
                  }}
                >
                  <AnalyticsWidgetSummary
                    title="Videos"
                    total={71}
                    icon={'material-symbols-light:subscriptions-rounded'}
                  />
                </Grid>
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={3}
                  onClick={() => {
                    navigate('/dashboard/video/video');
                  }}
                >
                  <AnalyticsWidgetSummary title="Assesment" total={131} color="info" icon={'octicon:organization-16'} />
                </Grid>
              </>
              :
              <>
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={4}
                  onClick={() => {
                    navigate('/dashboard/pdf/pdf');
                  }}
                >
                  <AnalyticsWidgetSummary title="PDF" total={131} color="info" icon={'octicon:organization-16'} />
                </Grid>
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={4}
                  onClick={() => {
                    navigate('/dashboard/video/video');
                  }}
                >
                  <AnalyticsWidgetSummary
                    title="Videos"
                    total={71}
                    icon={'material-symbols-light:subscriptions-rounded'}
                  />
                </Grid>
                <Grid
                  item
                  xs={12}
                  sm={6}
                  md={4}
                  onClick={() => {
                    navigate('/dashboard/assesment/assesment');
                  }}
                >
                  <AnalyticsWidgetSummary title="Assesment" total={131} color="info" icon={'octicon:organization-16'} />
                </Grid>
              </>
            }

            {/* <Grid
              item
              xs={12}
              sm={6}
              md={4}
              // onClick={() => {
              //   navigate('/dashboard/spell');
              // }}
            >
              <AnalyticsWidgetSummary title="All Users" total={319} color="warning" icon={'bx:clinic'} />
            </Grid> */}
          </>
        </Grid>
      </Container>
    </Page>
  );
}
